/* Header */
#logo {
  background-image: url(/static/media/logo.7e7de3c4.png);
  width: auto;
  height: 40px;
  padding-right: 5px;
  font-family: 'Pacifico', cursive;
  font-size: 1.5rem; }

#header-nav-links {
  font-family: sans-serif;
  font-size: 1rem; }

#header-nav {
  background-color: #53aa7d;
  background-image: linear-gradient(45deg, #53aa7d 0%, #00c958 100%);
  box-shadow: 0 0px 3px rgba(0, 0, 0, 0.75); }

#nav-title {
  font-family: 'Pacifico', cursive;
  font-size: 2rem;
  text-shadow: 2px 2px #32028a; }

/* Main */
#map {
  min-height: 800px;
  width: 100%;
  border-radius: 25px; }

/* NewsFeed */
/* NewsCard */
.news-card, #map {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  border: none; }

.card-container {
  max-height: 800px;
  border-radius: 25px; }

.news-card, .news-card:hover, .news-card:focus, .news-card:active {
  text-decoration: none;
  color: inherit;
  transition: background-color 300ms ease-out; }

.news-card:hover {
  background-color: #ebe2e2e3;
  transition: all 50ms ease-out; }

.article-thumbnail {
  object-fit: cover;
  max-width: 100px; }

.article-title {
  font-size: 1rem;
  display: inline; }

.save-button {
  padding: 0px 7px; }

.article-description, .article-publisher, .article-published-date {
  font-size: .75rem;
  line-height: 1rem; }

#scroll-bar::-webkit-scrollbar-track {
  background-color: #000000; }

#scroll-bar::-webkit-scrollbar {
  width: 7px;
  background-color: #F5F5F5; }

#scroll-bar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #FFF;
  background-image: -webkit-gradient(linear, 40% 0%, 75% 84%, from(#00c958), to(#01a849d7), color-stop(0.6, #54DE5D)); }

/* Media Queries */
@media only screen and (max-width: 425px) {
  .article-title {
    font-size: .75rem; }
  .article-description {
    display: none; }
  .article-thumbnail {
    height: 70px; }
  #map {
    height: 580px; } }


